.play-btn {
  .btn();
  width: 51px;
  height: 51px;

  color: @color-play-yellow;

  &:hover {
    color: rgba(@color-play-yellow, 0.7);
  }

  &:focus-visible {
    color: rgba(@color-play-yellow, 0.7);
    outline: none;
  }

  &:active {
    color: rgba(@color-play-yellow, 0.5);
  }
}
